
import { Category, Price } from "@/interface";
import { defineComponent } from "vue";
import { useRoute } from "vue-router";
import ActionButtonComponent from "../components/ActionButtonComponent.vue";

export default defineComponent({
  props: ["categories"],
  emits: ["showItemDetail", "updateItemPriceToOrder"],
  components: {
    ActionButtonComponent,
  },
  computed: {
    category(): Category {
      const categoryId = +useRoute().params.categoryId;
      const category = this.categories.find(
        (c: Category) => c.id === categoryId
      );
      if (category) {
        window.document.title = `${category.name} - Menu Panda`;
      }
      return category ? category : [];
    },
  },
  methods: {
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    updateItemPriceToOrder(itemPrice: Price) {
      this.$emit("updateItemPriceToOrder", { itemPrice, qty: 1 });
    },
  },
});
